.about {
  overflow: hidden;
  height: 100%; }

.about__p {
  padding: 10px 20px;
  width: 70%; }

.about__p-0 {
  border: 2px solid rgba(123, 10, 25, 0.5);
  background-color: rgba(123, 10, 25, 0.5);
  -webkit-animation: slidingP0 800ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
          animation: slidingP0 800ms cubic-bezier(0.445, 0.05, 0.55, 0.95); }

@-webkit-keyframes slidingP0 {
  from {
    -webkit-transform: translateY(900px);
            transform: translateY(900px);
    opacity: 0; } }

@keyframes slidingP0 {
  from {
    -webkit-transform: translateY(900px);
            transform: translateY(900px);
    opacity: 0; } }

